function Hello({ compiler, framework }) {
    return (
        <h1 >
            Hello from {compiler} and {framework}!
        </h1>
    );
}

Hello.propTypes = {
    compiler: React.PropTypes.string,
    framework: React.PropTypes.string,
};

Hello.defaultProps = {
    compiler: '',
    framework: '',
};

export default Hello;


